          'deletetable':1,
          'inserttable':1,
          'cellvalign':1,
          'insertcaption':1,
          'deletecaption':1,
          'inserttitle':1,
          'deletetitle':1,
          "mergeright":1,
          "mergedown":1,
          "mergecells":1,
