                  var attrNode = attributes[k];
                  if ( attrNode.nodeName != 'style' && attrNode.nodeName != 'class' && (!browser.ie || attrNode.specified) ) {
                      tgt.setAttribute(attrNode.nodeName, attrNode.nodeValue);
                  }
